.b-content {
  display: flex;
  justify-content: center;
  padding: 0 4%;
  margin-bottom: 50px;
}

.b-content__insites {
  width: calc(49% - 10px);
  margin: 0 10px;
  max-width: 675px;
}

.b-content__linkHeader {
  color: black;
}

.b-content__publication {
  width: calc(49% - 10px);
  margin: 0 10px;
  max-width: 675px;
}

.b-content__blockForContent {
  border-top: 1px solid #ccc;
  margin: 10px 0 0;
  padding: 10px 0 0;
  display: flex;
  flex-wrap: wrap;
  width: 100%;
}

.b-content__oneInsite {
  width: 100%;
}

.b-content__oneInsite--h2 {
  font-size: 18px;
  line-height: 1.3;
  font-weight: 600;
}

.b-content__oneInsite--p {
  font-size: 14px;
}

.b-content__pic {
  max-width: 100%;
  height: 168px;
  width: 39%;
  vertical-align: top;
  margin: 17px 10px 0 0;
  background-position: 0;
  background-size: cover;
  position: relative;
  display: inline-block;
}

.b-content__date_oneInsite {
  position: absolute;
  text-align: center;
  margin: 0;
  top: 30px;
  right: 25px;
  color: #fff;
  padding: 5px 10px 0;
  background: rgba(0, 0, 0, 0.3);
  z-index: 300;
}

.b-content__text_oneInsite {
  width: 58%;
  display: inline-block;
}

.b-content__label {
  height: 75px;
  width: 24%;
  vertical-align: top;
  background-repeat: no-repeat;
  background-size: auto 64px;
  margin: 15px 0 0;
  display: inline-block;
}

.b-content__onePublic {
  display: inline-block;
  width: 49%;
  margin: 0 0 18px;
}

.b-content__pub {
  width: 70%;
  margin: 6px 0 0;
  display: inline-block;
}

.b-content__pub--h3 {
  font-size: 16px;
  font-weight: 600;
  margin-top: 10px;
  line-height: 1.3;
}

.b-content__pub--h4 {
  font-size: 16px;
  line-height: 1.4;
  margin: 20px 0 0;
}

.b-content__pub--p {
  margin: 0;
  color: #5d5d5d;
  font-size: 14px;
}




