.foundation-mq {
  font-family: "small=0em&medium=48em&large=64em&xlarge=80em";
}

html {
  box-sizing: border-box;
  font-size: 16px;
}

*,
*::before,
*::after {
  box-sizing: inherit;
}

body {
  margin: 0;
  padding: 0;
  background: #fefefe;
  font-family: "Helvetica Neue", Helvetica, Roboto, Arial, sans-serif;
  font-weight: normal;
  line-height: 1.4;
  color: #0a0a0a;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
}

img {
  display: inline-block;
  vertical-align: middle;
  max-width: 100%;
  height: auto;
  -ms-interpolation-mode: bicubic;
}

textarea {
  height: auto;
  min-height: 50px;
  border-radius: 0;
}

select {
  box-sizing: border-box;
  width: 100%;
  border-radius: 0;
}

.map_canvas img,
.map_canvas embed,
.map_canvas object,
.mqa-display img,
.mqa-display embed,
.mqa-display object {
  max-width: none !important;
}

button {
  padding: 0;
  appearance: none;
  border: 0;
  border-radius: 0;
  background: transparent;
  line-height: 1;
  cursor: auto;
}

[data-whatinput='mouse'] button {
  outline: 0;
}

pre {
  overflow: auto;
}

button,
input,
optgroup,
select,
textarea {
  font-family: inherit;
}

.is-visible {
  display: block !important;
}

.is-hidden {
  display: none !important;
}

body {
  min-width: 320px;
}

main h1,
main .h1,
main .c-h1 {
  color: #0a0a0a;
  font-weight: bold;
  line-height: 1;
}

main h2,
main .h2,
main .c-h2 {
  color: #0a0a0a;
  font-weight: normal;
  line-height: 1;
}

main h3,
main .h3,
main .c-h3 {
  color: #8a8a8a;
  font-weight: normal;
}

main big,
main .big,
main .c-big {
  color: #0a0a0a;
  font-size: 24px;
}

main b,
main strong {
  font-weight: bold;
}

main hr {
  border: none;
  border-bottom: 2px solid #8a8a8a;
}

main ul {
  padding-left: 30px;
}

main ul li {
  margin-bottom: 10px;
}

main ol {
  padding-left: 30px;
}

main ol li {
  margin: 10px 0;
}

main a {
  color: #cacaca;
}

main a:hover {
  text-decoration: none;
}

main .c-right {
  float: right;
  margin: 0 0 10px 10px;
}

main .c-left {
  float: left;
  margin: 0 10px 10px 0;
}

main .c-black {
  color: #0a0a0a;
}

main .c-gray {
  color: #8a8a8a;
}

main .c-hidden {
  display: none;
}

@media screen and (max-width: 47.9375em) {
  main .c-small-hidden {
    display: none;
  }
}

@media screen and (min-width: 48em) and (max-width: 63.9375em) {
  main .c-medium-hidden {
    display: none;
  }
}

@media screen and (min-width: 64em) and (max-width: 79.9375em) {
  main .c-large-hidden {
    display: none;
  }
}

@media screen and (min-width: 80em) {
  main .c-xlarge-hidden {
    display: none;
  }
}




